.sl-prod { 
    display: flex;
    gap: 2.5vw;
}

.sl-prod__wr-big-img {
    position: relative;

    width: 21.46vw;
    min-width: 21.46vw;
    height: 100%;
    padding-top: 1.98vw;
}

.sl-prod__big-img {
    position: relative;

    width: 100%;

    opacity: 0;
}

.sl-prod__big-img_visible {
    opacity: 1;
}

.sl-prod__big-img_deco {
    position: absolute;
    top: 53%;
    left: 50%;
    
    height: 84%;
    width: 80%;

    box-shadow: 0.78vw 1.04vw 3.02vw 0.63vw rgba(0, 0, 0, 1);

    transform: translate(-50%, -50%);
}

.sl-prod__wr-descr-preview {
    display: flex;

    flex-direction: column;
    flex-grow: 1;
}

.sl-prod__text-about {
    width: 66.2%;
    padding-right: 14.6%;

    align-self: flex-end;

    opacity: 0;
}

.sl-prod__text-about_visible {
    opacity: 1;
}

.sl-prod__header {
    position: relative;
}

.sl-prod__title {
    width: fit-content;

    font-family: 'SofiaSans';
    font-size: 2.08vw;
    font-weight: 700;
    text-transform: uppercase;

    color: #F1551C;
}

.sl-prod__description {
    margin-top: 2.29vw;
}

.sl-prod__taste {
    display: flex;
}

.sl-prod__taste-title {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 700;
    text-transform: uppercase;

    color: #FFF;
}

.sl-prod__wr-taste-text {
    position: relative;

    flex-grow: 1;
}

.sl-prod__taste-text {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 400;

    color: #FFF;
}

.sl-prod__aroma {
    position: relative;

    display: flex;
    margin-top: 2.08vw;
}

.sl-prod__aroma-title {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 700;
    text-transform: uppercase;

    color: #FFF;
}

.sl-prod__wr-aroma-text {
    position: relative;

    flex-grow: 1;
}

.sl-prod__aroma-text {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 400;

    color: #FFF;
}

.sl-prod__line {
    width: 100%;
    height: 1.5px;
    margin-top: 1.41vw;

    background-color: #A59F9E;;
}

.sl-prod__character-list {
    margin-top: 1.20vw;
}

.sl-prod__character-item {
    position: relative;

    display: flex;

    justify-content: space-between;
}

.sl-prod__prop {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 400;
    text-transform: uppercase;

    color: #FFF;
}

.sl-prod__val {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 400;

    color: #FFF;
}




.sl-prod__wr-prev {
    position: relative;

    height: 25.2vw;
    margin-top: 2.97vw;
}

.sl-prod__wr-slides {
    height: 100%;
    padding-left: 0.65vw;

    overflow: hidden;
}

.sl-prod__slides {
    display: flex;
    gap: 3.49vw;

    align-items: center;
}

.sl-prod__slide {
    display: flex;

    width: 14.58vw;
    height: 21.35vw;

    border-radius: 0.42vw;
    background: #726B68;
    box-shadow: 0.78vw 0.78vw 0.94vw 0.36vw rgba(0, 0, 0, 0.3);

    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
}

.sl-prod__slide_active {
    width: 16.15vw;
    height: 22.92vw;
}

.sl-prod__wr-img-slide {
    position: relative;

    width: 42.528%; /*6.20vw*/
    height: 61.845%; /*13.18vw*/
    margin-top: 1.25vw;
}

.sl-prod__img-slide {
    position: relative;

    width: 100%;
    height: 100%;

    object-fit: contain;
    object-position: center;

    z-index: 2;
}

.sl-prod__img-slide-deco {
    position: absolute;
    top: 46%;
    left: 50%;

    width: 45.528%;
    height: 80.845%;

    box-shadow: 0.78vw 1.04vw 3.02vw 0.63vw rgba(0, 0, 0, 1);

    transform: translate(-50%, -50%);

    z-index: 1;
}

.sl-prod__wr-title-slide {
    margin-top: 0.83vw;
}

.sl-prod__title-slide {
    font-family: 'SofiaSans';
    font-size: 1.25vw;
    font-weight: 700;
    text-transform: uppercase;

    color: #A59F9E;
}

.sl-prod__wr-button-slide {
    position: relative;

    margin-top: 1.04vw;
}

.sl-prod__wr-button-slide-deco {
    position: absolute;
    top: 0;
    left: 50%;
    
    width: 120%;
    height: 100%;

    opacity: 0;

    border-radius: 50%;
    background: linear-gradient(180deg, rgba(114, 107, 104, 0.04) 15.09%, rgba(114, 107, 104, 0.8));

    transform: translate(-50%, 12%);

    pointer-events: none;
}

.sl-prod__slide_active .sl-prod__wr-button-slide-deco {
    opacity: 1;
}

.sl-prod__button-slide {
    display: flex;
    width: 8.75vw;
    height: 2.60vw;

    font-family: 'SofiaSans';
    font-size: 1.04vw;
    font-weight: 400;

    color: #fff;
    border-radius: 1.56vw;
    background: #F1551C;
    box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.15),
     5px 3px 6px 0px rgba(0, 0, 0, 0.13),
      11px 7px 8px 0px rgba(0, 0, 0, 0.08),
       20px 12px 9px 0px rgba(0, 0, 0, 0.02),
        31px 18px 10px 0px rgba(0, 0, 0, 0.00);

    justify-content: center;
    align-items: center;
}

.sl-prod .slider__arrow {
    position: absolute;
    top: 50%;

    transform: translate(0, -50%);
}

.sl-prod .slider__arrow-next {
    right: 2%;

    display: block;
}





@media( max-width: 961px ) {
    .sl-prod__wr-big-img {
        display: none;
    }
}

@media( max-width: 961px ) {
    .sl-prod__text-about {
        width: 90%;
        padding-right: 0;

        align-self: center;
        order: 2;
    }
}

