    .sl-prod__header {
        margin-top: 10.67vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__title {
        width: 100%;
        font-size: 6.40vw;
        text-align: center;
    }
}

@media( max-width: 961px ) {
    .sl-prod__taste-title {
        font-size: 4.27vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__taste-text {
        font-size: 3.73vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__aroma {
        margin-top: 6.4vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__aroma-title {
        font-size: 4.27vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__aroma-text {
        font-size: 3.73vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__line {
        margin-top: 3.6vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__character-list {
        margin-top: 6.8vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__prop {
        font-size: 4.27vw;
    }
}

@media( max-width: 961px ) {
