        font-size: 3.73vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__wr-prev {
        height: 128.80vw;
        margin-top: 0;

        background-color: #6B6462;

        order: 1;
    }
}

@media( max-width: 961px ) {
    .sl-prod__wr-slides {
        padding-left: 0;
        padding-top: 11vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__slides {
        gap: 0;

        align-items: initial;
    }
}

@media( max-width: 961px ) {
    .sl-prod__slide {
        width: 0;
        height: auto;

        background: none;
        box-shadow: none;

        flex-grow: 1;
        flex-shrink: 1;
        align-items: center;
    }

    .sl-prod__slide_active {
        width: 0;
        height: auto;
    }
}

@media( max-width: 961px ) {
    .sl-prod__wr-img-slide {
        width: 38.40vw;
        height: 82.13vw;
        margin-top: 0;
    }
}

@media( max-width: 961px ) {
    .sl-prod__img-slide-deco {
        top: 48.5%;
        left: 50%;
        
        width: 71.528%;
        height: 85.845%;

        box-shadow: 2.4vw 6.04vw 9.02vw 0.63vw rgba(0, 0, 0, 0.8);
    }
}

@media( max-width: 961px ) {
    .sl-prod__wr-title-slide {
        margin-top: 5.87vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__title-slide {
        width: fit-content;

        font-size: 5.33vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__wr-button-slide {
        width: fit-content;
        margin-top: 6.13vw;
    }
}

@media( max-width: 961px ) {
    .sl-prod__button-slide {
        width: 54.40vw;
        height: 10.67vw;

        font-size: 4.27vw;

        border-radius: 8vw;
    }
}


@media( max-width: 961px ) {
    .sl-prod .slider__arrow-prev {
        left: 2%;

        display: block;
    }

    .sl-prod .slider__arrow {
        top: 39.5%;
    }
}

